//Fonts
$poppins: 'Poppins', sans-serif;
$muli: 'Muli', sans-serif;

// Font Size
$bodySize: 1.125rem;
$h1-fontSize: 2.5rem;
$h2-fontSize: 2.125rem;
$h3-fontSize: 1.75rem;
$h4-fontSize: 1.375rem;
$l-fontSize: 1rem;
$sm-fontSize: 0.875rem;
$smallest-fontSize: 0.75rem;

//Colors for Light Mode
$backgroundColorLt: #f1f3f6;
$baseColorLt: #6c757d;
$primaryColorLt: #343a40;
$secondaryColorLt: #5f5f5f;
$boxShadow: 0 5px 15px rgba(0,0,0,.1);

//Colors for Dark Mode
$backgroundColorDt: #343a40;
$baseColorDt: #ffffff;
$primaryColorDt: #ffffff;
$secondaryColorDt: #d6d6d6;
$boxShadowDt: 2px 0px 5px rgba(200, 200, 200, .5);

:root {
  --background-color: #{$backgroundColorLt};
  --base-color: #{$baseColorLt};
  --primary-color: #{$primaryColorLt};
  --secondary-color: #{$secondaryColorLt};
  --box-shadow: #{$boxShadow};
}

[data-theme="dark"] {
  --background-color: #{$backgroundColorDt};
  --base-color: #{$baseColorDt};
  --primary-color: #{$primaryColorDt};
  --secondary-color: #{$secondaryColorDt};
  --box-shadow: #{$boxShadowDt};
}